import ScrollMagic from 'scrollmagic';
import 'scrollmagic/scrollmagic/uncompressed/plugins/animation.gsap';
import $ from 'jquery';
import gsap from 'gsap';

window.addEventListener('load', () => {
  let design_items = document.querySelectorAll('.design_item');
  // let tl = new TimelineMax();

  let controller = new ScrollMagic.Controller();

  new ScrollMagic.Scene()
    .addTo(controller);

  for (let design_item of design_items) {

    let
      title = $(design_item).children('.design_item__title'),
      title_span = title.children(),
      text = $(design_item).children('.design_item__text');

    let tween_title = gsap
      .staggerFromTo($(title_span), 0.5, {y: -30, opacity: 0}, {y: 0, opacity: 1}, 0.1);

    let tween_text = gsap
      .fromTo($(text), 0.5, {y: 30, opacity: 0}, {y: 0, opacity: 1, delay: 0.5});



    let scene_1 = new ScrollMagic.Scene({triggerElement: design_item, duration: 0, offset: -300})
      .setTween(tween_title)
      .addTo(controller);

    let scene_2 = new ScrollMagic.Scene({triggerElement: design_item, duration: 0, offset: -300})
      .setTween(tween_text)
      .addTo(controller);
  }


});
